ul {
    display: flex;
    list-style-type: disc;
    margin-block-start: 1em;
    margin-block-end: 1em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    padding-inline-start: 40px;
}


ul.horizontal {
    
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: #333;
}



ul.horizontal li a {
    display: inline-block;
    color: white;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
}

li {
    text-align: justify;
}

li:hover {
    background-color:green;
}

.clicked {
    background-color:white;
    color: black;
}

.rightli {
    float: right;
}

.home-card {
    background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(125,151,221,0.9332107843137255) 0%, rgba(127,234,238,1) 96%);
    display: flex;
    margin-top: 30px;
    margin-left: 20px;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    width: 97%;
    height: 500px;    
}
.card {
    position: relative;
    margin-top: 40px;
    margin-left: 30px;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    width: 32%;
    height: 200px;
    background-color:white;
  }
  
  .card:hover {
    box-shadow: 0 20px 40px 0 rgba(0,0,0,0.2);
  }
  
  .container {
    padding: 2px 16px;
  }


input {
      width: 200px;
      height: 30px;
      font-size: 18px;
      margin-left:20px; 
}

button {
    margin-left: 20px;
    width: 80px;
    height: 35px;
    font-size: 20px;
    border: none;
}

button:hover {
    cursor: pointer;
}

button:focus {
    outline: none;
}

h3 {
    margin-left: 20px;
    margin-top: 50px;
}

.data-card {
    display: flex;
    justify-content: space-evenly;
    flex-direction: column;
    margin-top: 40px;
    margin-left: 30px;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    width: 63%;
    height: 420px;
    background-color:white;
}

.data-card:hover {
    box-shadow: 0 20px 40px 0 rgba(0,0,0,0.2);
}

.lblNames {
    margin-top: 20px;
    display: flex;
    justify-content: space-evenly;    
}


label {
    
    margin-left: 20px;
    font-size: 30px; 
}

.imgdiv {
    margin-top: 20px;
    display: flex;
    justify-content: space-evenly;  
}

.imgdiv img {
    
    margin-left: 20px;
    height: 100px;
    width: 100px;
}


.exp-card {
    position: relative;
    margin-top: 40px;
    margin-left: 30px;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    width: 32%;
    height: 330px;
    background-color:white;
}

.exp-card:hover {
    box-shadow: 0 20px 40px 0 rgba(0,0,0,0.2);
  }
  
.exp-data-card {
    display: flex;
    
    flex-direction: column;
    margin-top: 40px;
    margin-left: 30px;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    width: 63%;
    height: 420px;
    background-color:white;
}

.exp-data-card:hover {
    box-shadow: 0 20px 40px 0 rgba(0,0,0,0.2);
}

.exp-imgdiv {
    margin-top: 20px;
    display: flex;
    justify-content: space-evenly;  
}

.exp-imgdiv img {
    
    margin-left: 20px;
    height: 50px;
    width: 50px;
}

.exp-imgdiv label {
    
    margin-left: 20px;
    font-size: 20px; 
}


.exp-lblNames {
    margin-top: 10px;
    display: flex;
    justify-content: space-evenly;    
}


.exp-lblNames label {
    
    margin-left: 20px;
    font-size: 20px; 
}

th,td {
    width: 200px;
    height: 30px;
}
  
table, th, td 
{
    border: solid 2px gray;
    border-collapse: collapse;
    padding: 2px 3px;
    text-align: center;
}


.exp-table {
    margin-top: 10px;
    margin-left: 20px;
    margin-right: 20px;
    display: flex;
    justify-content: space-evenly;
    overflow-y: scroll;
    height: 250px;
}

.expAmt {
    color: red;
}

.exp-table button {
    color: red;
    background-color: transparent;
    border: none;
}